import {comingSoon,newMovies,top250} from "../../apis"
export default {
	data() {
		return {
			moviesList:[]
		}
	},
	onReady(){
		this.pageInit();
	},
	computed:{
		nowPlayingList(){
			return this.$store.state.nowPlayingList;
		}
	},
	methods: {
		pageInit(){
			uni.showLoading({
			    title: '加载中'
			});
			Promise.all([
				this._comingSoon(),
				this._newMovies(),
				this._top250()
				]).then((res)=>{
					uni.hideLoading();
					let rel=[];
					res.map((item)=>{
						rel.push(item[1].data)
					})
					this.moviesList=rel;
					console.log(this.moviesList)
				})
		},
		toDtails(id){
			uni.navigateTo({
				url:"../dtails/dtails?id="+id
			})
		},
		toClass(title){
			uni.navigateTo({
				url:"../class/class?title="+title
			})
		},
		_comingSoon(){
			return comingSoon({
				city:this.$store.state.city,
				start:"0",
				count:"8"
			})
		},
		_newMovies(){
			return 	newMovies({
				city:this.$store.state.city,
				start:"0",
				count:"8"
			})
		},
		_top250(){
			return top250({
				city:this.$store.state.city,
				start:"0",
				count:"8"
			})
		}
	}
}
